@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;800&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: 'Open Sans', sans-serif;
  background-color: #172843;
  color:#FFFFFF;
}


/* banner style */
.banner{
  padding: 4.69em 4.44em;
}

header {
  max-width: 22.75em;
  height: auto;
  margin-bottom: 7em;
}

.banner-content {
  font-family: 'Nunito', sans-serif;
  line-height: 24px;
  letter-spacing: 1px;
  max-width: 22.75em;
}

.banner-content h5, p {
  padding-bottom: 1.2em;
}

/* bg image style */

.bg-img {
  max-width: 1367px;
  position: absolute;
  top: 0;
  left: 553px;
}


/* bottom nav style */

.bottom-nav {
  background: linear-gradient(#172843, #000000);
  height: 16.25em;
  width: 1920px;
}

.nav-links {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-end;
  padding-top: 11em;
  max-width: 100%;
}

.nav-links li {
  list-style: none;
  padding: 0.35em 1em;
}

.nav-links li a {
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  font-weight: 800;
  color: #FFFFFF;
  padding: 0 0.8em 0.5em;
}

.nav-links li a:hover {
  border-bottom: 3px solid #55C6D7;
}

.home-icon {
  padding: 0 0.3em;
}



